import React from 'react'
import Icon from 'components/Icon-svg'
import SwitchCheck from 'components/Switch'
import TitleBar from 'components/topbar'
import { Local } from 'utils/storage'
import connect from 'connect'
import {withRouter, Redirect} from 'react-router-dom'
import {  Button, WhiteSpace, WingBlank, List, InputItem, Flex, Switch, Picker, Modal, PickerView } from 'antd-mobile';
import './index.less'
@connect
@withRouter
export default class extends React.Component {
    state = {
        info: {
            send: Local.get('system_send') || false,
            list: Local.get('system_list') || false
        }
    }
    save (attr, val) {
        let obj = this.state.info
        obj[attr] = val
        this.setState(obj)
        Local.set({
            system_send: this.state.info.send,
            system_list: this.state.info.list
        })
    }
    signOut () {
        const {signOut, setFooterList, history} = this.props
        this.props.signOut()
        setFooterList({title: '未登录', icon: 'account', path: '/account'})
        history.slideStatus = 'right'
        history.push('/account')
    }
    render () {
        const {info} = this.state
        const Item = List.Item;
        const Brief = Item.Brief;
        return (
            <div className="system-wrapper">
                <TitleBar title="设置"/>
                <List className="my-list">
                        <Item arrow="horizontal" onClick={() => {
                            this.props.history.slideStatus = 'left'
                            this.props.history.push(`/changepass`)
                        }}>更换手机号</Item>
                        <Item arrow="horizontal" onClick={() => {
                            this.props.history.slideStatus = 'left'
                            this.props.history.push(`/feedback`)
                        }}>意见反馈</Item>
                </List>
                
                <div className="cardfooter">
                    <Button>退出当前账号</Button>
                </div>
                
            </div>
        )
    }
}


